.from-work #building {
    opacity: 0.5;
}

#building {
    text-align: right;
    padding: 25px;

    >h4 {
        font-size: 25px;
        font-weight: 700;
    }

    >.information {
        display: flex;
        justify-content: space-between;
        align-items: center;

        >div {
            >.description {
                font-weight: bold;
                max-width: 400px;
                margin: 15px 0px;
            }

            >.effect {
                margin: 15px 0px;

                td:last-child {
                    padding-right: 10px;
                }
            }
        }

        >img {
            align-self: flex-start;
        }
    }
}